/* src/css/Herosection.css */

/* ---------------------------------------------------- */
/* 1. Í∏∞Î≥∏ ÏòÅÏõÖ ÏÑπÏÖò Ïª®ÌÖåÏù¥ÎÑà Î∞è Ïù¥ÎØ∏ÏßÄ ÎûòÌçº */
/* ---------------------------------------------------- */
.hero-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.hero-image-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(60%);
  transition: opacity 0.5s ease-in-out;
}

/* ---------------------------------------------------- */
/* 2. Ï§ëÏïô ÌÖçÏä§Ìä∏ Ïò§Î≤ÑÎ†àÏù¥ */
/* ---------------------------------------------------- */
.hero-text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  /* üí° z-index: 10; Ï†úÍ±∞ - Î©îÎâ¥Ïùò z-indexÏóê ÏùòÏ°¥ÌïòÏó¨ ÍπîÎÅîÌïòÍ≤å Ï≤òÎ¶¨ */
  width: 90%;
}

.hero-text-overlay h1 {
  font-size: 3rem; /* PC Í∏∞Ï§Ä */
  margin-bottom: 1rem;
  line-height: 1.2;
}

.hero-text-overlay p {
  font-size: 1.2rem; /* PC Í∏∞Ï§Ä */
  margin-bottom: 2rem;
}

.view-menu-btn {
  background-color: #ffda79;
  color: #333;
  padding: 12px 25px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.view-menu-btn:hover {
  background-color: #ffc241;
}

/* ---------------------------------------------------- */
/* 3. Ïù¥ÎØ∏ÏßÄ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î≤ÑÌäº (Ïä¨ÎùºÏù¥Îçî) */
/* ---------------------------------------------------- */
.nav-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  cursor: pointer;
  font-size: 2rem;
  /* üí° z-index: 15; Ï†úÍ±∞ */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease;
}

.nav-btn:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.prev-btn {
  left: 20px;
}

.next-btn {
  right: 20px;
}

/* ---------------------------------------------------- */
/* 4. Î™®Î∞îÏùº Î∞òÏùëÌòï Ïä§ÌÉÄÏùº (Mobile Responsive) */
/* ---------------------------------------------------- */
@media screen and (max-width: 768px) {
  /* ÌÖçÏä§Ìä∏ Ïò§Î≤ÑÎ†àÏù¥ ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  .hero-text-overlay h1 {
    font-size: 2rem; /* h1 Ìè∞Ìä∏ ÌÅ¨Í∏∞ Ï∂ïÏÜå */
    margin-bottom: 0.5rem;
  }

  .hero-text-overlay p {
    font-size: 1rem; /* p Ìè∞Ìä∏ ÌÅ¨Í∏∞ Ï∂ïÏÜå */
    margin-bottom: 1.5rem;
  }

  /* Î≤ÑÌäº ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  .view-menu-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }

  /* ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î≤ÑÌäº ÌÅ¨Í∏∞ Î∞è ÏúÑÏπò Ï°∞Ï†ï */
  .nav-btn {
    width: 40px;
    height: 40px;
    font-size: 1.5rem; /* ÌôîÏÇ¥Ìëú ÌÅ¨Í∏∞ Ï∂ïÏÜå */
  }

  .prev-btn {
    left: 10px; /* Í∞ÄÏû•ÏûêÎ¶¨ Í∞ÑÍ≤© Ï∂ïÏÜå */
  }

  .next-btn {
    right: 10px; /* Í∞ÄÏû•ÏûêÎ¶¨ Í∞ÑÍ≤© Ï∂ïÏÜå */
  }
}
