/* src/css/Header.css - ÏôºÏ™Ω ÌÜµÌï© Î©îÎâ¥ + PC Î°úÍ∑∏Ïù∏ Î≥µÍµ¨ */

/* ---------------------------------------------------- */
/* 1. Í∏∞Î≥∏ Ìó§Îçî Ïª®ÌÖåÏù¥ÎÑà (PC Í≥µÌÜµ) */
/* ---------------------------------------------------- */
.main-header {
