}

/* ---------------------------------------------------- */
/* 2. Î°úÍ≥† / PC ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò */
/* ---------------------------------------------------- */
.header-logo a {
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
  color: #fff;
}

.header-nav ul {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
}

.header-nav li {
