/* src/css/IntroPage.css */

.intro-page-layout {
  width: 100%;
  /* HeaderÍ∞Ä fixed/absoluteÏùº Í≤ΩÏö∞, ÏΩòÌÖêÏ∏†Í∞Ä Header Îí§Î°ú Ïà®ÏßÄ ÏïäÎèÑÎ°ù padding-top Ï∂îÍ∞Ä */
  padding-top: 80px; /* Header ÎÜíÏù¥Ïóê Îî∞Îùº Ï°∞Ï†à */
  background-color: #f8f8f8; /* Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄÏùò Í∏∞Î≥∏ Î∞∞Í≤ΩÏÉâ */
}

/* --- 1. ÏÉÅÎã® Î©îÏù∏ Hero ÏÑπÏÖò --- */
.intro-main-hero {
  height: 70vh; /* Î∑∞Ìè¨Ìä∏ ÎÜíÏù¥Ïùò 70% */
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white; /* ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ */
  overflow: hidden; /* Ïò§Î≤ÑÌîåÎ°úÏö∞ Î∞©ÏßÄ */
}

.intro-main-hero::before {
  /* Ïñ¥ÎëêÏö¥ Ïò§Î≤ÑÎ†àÏù¥ */
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Î∞òÌà¨Î™Ö Í≤ÄÏùÄÏÉâ Ïò§Î≤ÑÎ†àÏù¥ */
