}

.intro-sub-heading {
  font-size: 1.1rem;
  letter-spacing: 4px;
  font-weight: 300;
  margin-bottom: 15px;
  color: #ffda79; /* Ìè¨Ïù∏Ìä∏ ÏÉâÏÉÅ */
}

.intro-main-hero h1 {
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 25px;
}

.intro-description {
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.6;
}

/* --- 2. ÌöåÏÇ¨ Ï≤†Ìïô / ÎπÑÏ†Ñ ÏÑπÏÖò --- */
.intro-vision-section {
  max-width: 900px;
  margin: 80px auto; /* Ï§ëÏïô Ï†ïÎ†¨, ÏÉÅÌïò Ïó¨Î∞± */
  padding: 0 20px;
  text-align: center;
  color: #333;
}

.intro-vision-section h2 {
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 30px;
}

.intro-vision-section p {
