  color: #555;
  margin-bottom: 40px;
}

.vision-divider {
  width: 100px;
  height: 3px;
  background-color: #ffda79; /* Ìè¨Ïù∏Ìä∏ ÏÉâÏÉÅ */
  border: none;
  margin: 40px auto; /* Ï§ëÏïô Ï†ïÎ†¨ */
}

/* --- 3. ÏÑúÎπÑÏä§ ÌïµÏã¨ Í∞ÄÏπò ÏÑπÏÖò (Ìù∞ÏÉâ Î∞∞Í≤Ω, Îë•Í∑º ÎîîÏûêÏù∏) --- */
.intro-values-section {
  /* Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ Í¥ÄÎ†® ÏÜçÏÑ± Ï†úÍ±∞ */
  background-image: none !important;
  background-color: #ffffff; /* Ìù∞ÏÉâ Î∞∞Í≤Ω */
  padding: 100px 20px;
  position: relative;
  color: #333;
  text-align: center;
}

/* Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ Ïò§Î≤ÑÎ†àÏù¥Îßå Ïà®ÍπÄ */
.intro-values-section::before {
  content: none;
  display: none;
}

.values-overlay {
  position: relative;
  z-index: 2;
  padding: 0;
  width: 100%;
}

.intro-values-section h2 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 70px;
  color: #333;
  text-shadow: none;
}

.value-items {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 50px;
  max-width: 1200px;
  margin: 0 auto;
}

.value-item {
  flex: 0 0 250px;
  width: 250px;
  height: 250px;
  padding: 20px;

  border-radius: 50%; /* Îë•Í∑º Î™®Ïñë */

  /* üõ†Ô∏è ÏàòÏ†ï: Í∏∞Î≥∏ Î∞∞Í≤ΩÏÉâÏùÄ Ìù∞ÏÉâÏúºÎ°ú Ïú†ÏßÄÌïòÍ≥†, Í∞Å ÏïÑÏù¥ÌÖúÎ≥ÑÎ°ú ÏÉâÏÉÅ ÏßÄÏ†ï */
  background-color: #fff;
  border: 1px solid #ddd; /* ÏñáÏùÄ ÌÖåÎëêÎ¶¨ */

  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* ÏûÖÏ≤¥Í∞ê Í∑∏Î¶ºÏûê */

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  text-align: center;
  transition: transform 0.4s ease, box-shadow 0.4s ease,
    background-color 0.4s ease; /* Î∞∞Í≤ΩÏÉâ Ìä∏ÎûúÏßÄÏÖò Ï∂îÍ∞Ä */
}

/* üõ†Ô∏è Ï∂îÍ∞Ä: Í∞Å value-itemÏóê Îπ®Í∞ï, Ï¥àÎ°ù, ÌååÎûë Î∞∞Í≤ΩÏÉâ Ï†ÅÏö© */
.value-items .value-item:nth-child(1) {
  background-color: #ff6b6b; /* Îπ®Í∞ÑÏÉâ Í≥ÑÏó¥ */
  color: #fff; /* Í∏ÄÏî®ÏÉâ Ìù∞ÏÉâÏúºÎ°ú */
}
.value-items .value-item:nth-child(2) {
  background-color: #2ecc71; /* Ï¥àÎ°ùÏÉâ Í≥ÑÏó¥ */
  color: #fff;
}
.value-items .value-item:nth-child(3) {
  background-color: #3498db; /* ÌååÎûÄÏÉâ Í≥ÑÏó¥ */
  color: #fff;
}

.value-item:hover {
  transform: translateY(-5px) scale(1.03);
  /* üõ†Ô∏è ÏàòÏ†ï: Ìò∏Î≤Ñ Ïãú Î∞∞Í≤ΩÏÉâÏùÄ Í≥†Ï†ïÎêú ÏÉâÏÉÅÏóêÏÑú ÏÇ¥Ïßù Ïñ¥Îë°Í≤å */
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}
.value-items .value-item:nth-child(1):hover {
  background-color: #e74c3c;
} /* Îπ®Í∞ÑÏÉâ Ìò∏Î≤Ñ */
.value-items .value-item:nth-child(2):hover {
  background-color: #27ae60;
} /* Ï¥àÎ°ùÏÉâ Ìò∏Î≤Ñ */
.value-items .value-item:nth-child(3):hover {
  background-color: #2980b9;
} /* ÌååÎûÄÏÉâ Ìò∏Î≤Ñ */

/* üõ†Ô∏è Ï†úÍ±∞: ÎÖ∏ÎûÄÏÉâ ÏõêÌòï ÏïÑÏù¥ÏΩò ÌîåÎ†àÏù¥Ïä§ÌôÄÎçî Ï†úÍ±∞ */
.value-item h3::before {
  content: none;
  display: none;
}

.value-item h3 {
  font-size: 1.5rem;
  color: inherit; /* üõ†Ô∏è ÏàòÏ†ï: Î∂ÄÎ™® (value-item)Ïùò color ÏÜçÏÑ± ÏÉÅÏÜç */
  margin-bottom: 10px;
  font-weight: 700;
}

.value-item p {
  font-size: 0.9rem;
  line-height: 1.5;
  color: inherit; /* üõ†Ô∏è ÏàòÏ†ï: Î∂ÄÎ™® (value-item)Ïùò color ÏÜçÏÑ± ÏÉÅÏÜç */
}

/* --- 4. ÌÅ¥Î°úÏßï / ÏΩúÌà¨Ïï°ÏÖò ÏÑπÏÖò --- */
.intro-cta-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #333; /* Ïñ¥ÎëêÏö¥ Î∞∞Í≤Ω */
  color: white;
}

