/* src/css/ServicePage.css */

.service-page-layout {
  width: 100%;
  padding-top: 0;
  background-color: #f8f8f8;
  min-height: 100vh;
  font-family: "Malgun Gothic", "Nanum Gothic", sans-serif;
}

/* -------------------------------------------------------- */
/* --- 1. ÏÉÅÎã® Î∞∞ÎÑà Hero ÏÑπÏÖò (Ïú†ÏßÄ) --- */
/* -------------------------------------------------------- */
.service-main-hero {
  height: 50vh;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  overflow: hidden;
}

.service-main-hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
  z-index: 1;
}

.service-hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 20px;
}

.service-sub-heading {
  font-size: 1.1rem;
  letter-spacing: 4px;
  font-weight: 300;
  margin-bottom: 15px;
  color: #ffda79;
}

.service-hero-content h1 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 25px;
}

.service-description {
  font-size: 1.1rem;
  font-weight: 300;
  line-height: 1.6;
}

/* -------------------------------------------------------- */
/* --- 2. ÏÑúÎπÑÏä§ Í∏∞Îä• Î∞¥Îìú ÏÑπÏÖò (LG Way Ïä§ÌÉÄÏùº) --- */
/* -------------------------------------------------------- */
.service-bands-wrapper {
  padding: 60px 0;
  text-align: center;
  background-color: #ffffff;
}

.bands-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #333;
}

.bands-subtitle {
  font-size: 1.1rem;
  color: #666;
  margin-bottom: 40px;
}

.feature-band-item {
  height: 300px; /* Î∞∞ÎÑà ÎÜíÏù¥ */
  background-size: cover;
  background-position: center;
  position: relative;
  overflow: hidden;
  margin-bottom: 2px; /* ÏÑπÏÖò ÏÇ¨Ïù¥Ïùò ÏñáÏùÄ Íµ¨Î∂ÑÏÑ†Ï≤òÎüº Î≥¥Ïù¥Í∏∞ ÏúÑÌï® */
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.band-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* üí° LG Way Ïä§ÌÉÄÏùºÏ≤òÎüº Ïñ¥ÎëêÏö¥ Í∑∏ÎùºÎç∞Ïù¥ÏÖò Ïò§Î≤ÑÎ†àÏù¥ */
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.6));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  padding: 20px;
}

.band-title {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 5px;
  position: relative;
  z-index: 2;
}

.band-subtitle-text {
  font-size: 1.1rem;
  font-weight: 300;
  margin-bottom: 20px;
  position: relative;
  z-index: 2;
}

.band-detail {
  max-width: 600px;
  opacity: 0; /* ÌèâÏÜåÏóêÎäî Ïà®ÍπÄ */
  transition: opacity 0.5s ease;
  position: relative;
  z-index: 2;
}

.feature-band-item:hover .band-detail {
  opacity: 1; /* Ìò∏Î≤Ñ Ïãú ÏÉÅÏÑ∏ ÎÇ¥Ïö© ÌëúÏãú */
}

.band-detail p {
  font-size: 0.95rem;
  margin-bottom: 15px;
  line-height: 1.5;
}

.band-detail-btn {
  padding: 10px 20px;
  background-color: #e67e22; /* Ìè¨Ïù∏Ìä∏ ÏÉâÏÉÅ */
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* -------------------------------------------------------- */
/* --- 3. ÌÅ¥Î°úÏßï / ÏΩúÌà¨Ïï°ÏÖò ÏÑπÏÖò (Ïú†ÏßÄ) --- */
/* -------------------------------------------------------- */
.service-cta-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #333;
  color: white;
}

.service-cta-section p {
  font-size: 1.3rem;
  margin-bottom: 30px;
}

.service-cta-btn {
  background-color: #ffda79;
  color: #333;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.service-cta-btn:hover {
  background-color: #f0c765;
}
