/* src/css/AboutPage.css */

.about-page-layout {
  width: 100%;
  padding-top: 0;
  background-color: #f8f8f8;
  min-height: 100vh;
}

/* -------------------------------------------------------- */
/* --- 1. ÏÉÅÎã® Î∞∞ÎÑà Hero ÏÑπÏÖò --- */
/* -------------------------------------------------------- */
.about-main-hero {
  height: 50vh;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  overflow: hidden;
}

.about-main-hero::before {
  /* Î∞òÌà¨Î™Ö Í≤ÄÏùÄÏÉâ Ïò§Î≤ÑÎ†àÏù¥ */
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
  z-index: 1;
}

.about-hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 20px;
}

.about-sub-heading {
  font-size: 1.1rem;
  letter-spacing: 4px;
  font-weight: 300;
  margin-bottom: 15px;
  color: #ffda79; /* Ìè¨Ïù∏Ìä∏ ÏÉâÏÉÅ */
}

.about-main-hero h1 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 25px;
}

.about-description {
  font-size: 1.1rem;
  font-weight: 300;
  line-height: 1.6;
}

/* -------------------------------------------------------- */
/* --- 2. Í∞úÎ∞úÏßÑ ÏÉÅÏÑ∏ ÏÜåÍ∞ú Ïπ¥Îìú ÏÑπÏÖò --- */
/* -------------------------------------------------------- */

.developer-detail-section {
  max-width: 1200px;
  margin: 60px auto;
  padding: 0 20px;
  text-align: center;
  background-color: #ffffff;
  color: #333;
}

.developer-section-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  color: #333;
}

.developer-section-description {
  font-size: 1.15rem;
  line-height: 1.8;
  color: #555;
  margin-bottom: 50px;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

/* --- ÌåÄÏõê Ïπ¥Îìú Í∑∏Î¶¨Îìú Ïä§ÌÉÄÏùº (Grid LayoutÏúºÎ°ú 3Ïó¥ Í≥†Ï†ï) --- */

.team-members-grid {
  /* üõ†Ô∏è 3Ïó¥ Grid LayoutÏúºÎ°ú Î™ÖÏãúÏ†Å Ï†ïÎ†¨ */
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 1frÏî© 3Í∞úÏùò ÎèôÏùºÌïú Ïó¥ */
  gap: 30px;

  max-width: 1200px;
  margin: 50px auto 0;
  padding: 0 20px;
}

.member-card {
  /* Grid Ïª®ÌÖåÏù¥ÎÑàÍ∞Ä ÎÑàÎπÑÎ•º Ï†úÏñ¥ */
  background-color: #fff;
  padding: 30px 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
  border: 1px solid #f0f0f0;
  transition: transform 0.3s ease;
  text-align: center;
}

.member-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.12);
}

.member-image-placeholder {
  width: 100px;
  height: 100px;
  background-color: #ffda79; /* Ìè¨Ïù∏Ìä∏ ÏÉâÏÉÅ */
  border-radius: 50%;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
  font-size: 0.9rem;
  overflow: hidden;
}

.member-name {
  font-size: 1.4rem;
  color: #2c3e50;
  margin-bottom: 5px;
  font-weight: 700;
}

.member-role {
  font-size: 0.9rem;
  color: #e67e22; /* Ïò§Î†åÏßÄ Í≥ÑÏó¥ Ìè¨Ïù∏Ìä∏ ÏÉâÏÉÅ */
  font-weight: 600;
  margin-bottom: 15px;
}

.member-description {
  font-size: 0.9rem;
  color: #666666;
  line-height: 1.6;
}

/* -------------------------------------------------------- */
/* --- 3. ÌÅ¥Î°úÏßï / ÏΩúÌà¨Ïï°ÏÖò ÏÑπÏÖò --- */
/* -------------------------------------------------------- */

.about-cta-section {
  padding: 80px 20px;
  text-align: center;
  background-color: #333;
  color: white;
  margin-top: 40px;
}

.about-cta-section p {
  font-size: 1.3rem;
  margin-bottom: 30px;
}

.about-cta-btn {
  background-color: #ffda79;
  color: #333;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

.about-cta-btn:hover {
  background-color: #f0c765;
}
