/* src/css/Form.css */

/* ------------------------------------------- */
/* 1. Î™®Îã¨ Ïò§Î≤ÑÎ†àÏù¥ (Î∞∞Í≤ΩÏùÑ Ïñ¥Îë°Í≤å ÎßåÎìúÎäî ÏòÅÏó≠) */
/* ------------------------------------------- */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7); /* Î∞òÌà¨Î™Ö Í≤ÄÏùÄÏÉâ Î∞∞Í≤Ω */
  z-index: 999; /* üí° z-index Ïú†ÏßÄ */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* ------------------------------------------- */
/* 2. Ìèº Ïª®ÌÖåÏù¥ÎÑà (Î™®Îã¨ Î∞ïÏä§) */
/* ------------------------------------------- */
.form-container {
  max-width: 400px;
  width: 90%;

  position: relative;
  margin: 0;
  padding: 40px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  text-align: center;

  overflow-y: auto;
  max-height: 90vh;
}

/* ------------------------------------------- */
/* 3. Ìèº Ï†úÎ™© Î∞è ÎÇ¥Î∂Ä Ïä§ÌÉÄÏùº (Ïú†ÏßÄ) */
/* ------------------------------------------- */

/* Ìèº Ï†úÎ™© */
.form-container h2 {
  margin-bottom: 30px;
  color: #333;
  font-size: 1.8rem;
  font-weight: 600;
}

/* Ìèº Ï†ÑÏ≤¥ Ï†ïÎ†¨ */
.auth-form {
  text-align: left;
}

/* Í∞Å ÏûÖÎ†• ÌïÑÎìú Í∑∏Î£π */
.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  color: #555;
  font-size: 0.95rem;
}

/* ÏûÖÎ†• ÌïÑÎìú (Input) */
.form-group input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
  transition: border-color 0.3s;
}

.form-group input:focus {
  border-color: #f7b731;
  outline: none;
}

/* Ï†úÏ∂ú Î≤ÑÌäº */
.submit-btn {
  width: 100%;
  padding: 12px;
  background-color: #f7b731;
  color: #333;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 10px;
  transition: background-color 0.3s;
}

.submit-btn:hover {
  background-color: #ffc107;
}

/* Ïò§Î•ò Î©îÏãúÏßÄ Ïä§ÌÉÄÏùº */
.error-message {
  color: #e74c3c;
  background-color: #fceae9;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 15px;
  text-align: center;
  font-size: 0.9rem;
}

/* ÌïòÎã® ÎßÅÌÅ¨ ÌÖçÏä§Ìä∏ (Ïòà: ÌöåÏõêÍ∞ÄÏûÖ ÎßÅÌÅ¨) */
.link-text {
  margin-top: 20px;
  font-size: 0.9rem;
  color: #777;
}

.link-text a {
  color: #3498db;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s;
}

.link-text a:hover {
  color: #2980b9;
}

/* ------------------------------------------- */
/* 4. Îã´Í∏∞ Î≤ÑÌäº Ïä§ÌÉÄÏùº (Z-INDEX ÏàòÏ†ï) */
/* ------------------------------------------- */
.close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: none;
  border: none;
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  cursor: pointer;
  line-height: 1;
  padding: 5px;
  transition: color 0.2s;

  /* üí° [ÌïµÏã¨ ÏàòÏ†ï] Î™®Îã¨ Î∞∞Í≤Ω(999)Î≥¥Îã§ ÎÜíÍ≤å ÏÑ§Ï†ïÌïòÏó¨ ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏Î•º Í∞ïÏ†úÎ°ú Î∞õÍ≤å Ìï® */
  z-index: 1000;
}

.close-btn:hover {
  color: #e74c3c;
}
