.admin-page-container {
  padding: 30px;
  padding-top: 70px; /* Ìó§Îçî Í≥µÍ∞Ñ ÌôïÎ≥¥ */
  background-color: #f9f9f9;
  min-height: 100vh;
  font-family: Arial, sans-serif;
